import React, {ReactNode} from 'react'
import {Breadcrumb, Button, Col, Divider, Drawer, Empty, notification, PageHeader, Row, Statistic, Tabs, Tag} from "antd";
import {SmileOutlined} from "@ant-design/icons";
import {useDispatch, useSelector} from "react-redux";
import {UIStateType} from "../reducers/UIReducer";
import store from "../store";
import {toggleSidebar} from "../actions/UIAction";

const {TabPane} = Tabs

const Project: React.FC<{}> = (props) => {
    const dispatch = useDispatch<typeof store.dispatch>()

    const [state, setState] = React.useState({ visible: false });

    const showDrawer = () => {
        setState({
            visible: true,
        })
    };

    const onClose = () => {
        setState({
            visible: false,
        });
    };


    return (
        <>

            <PageHeader
                onBack={() => window.history.back()}
                title="Title"
                tags={<Tag color="blue" onClick={showDrawer}>Running</Tag>}
                subTitle="This is a subtitle"
                extra={[
                    <Button key="3">Operation</Button>,
                    <Button key="2">Operation</Button>,
                    <Button key="1" type="primary" onClick={()=>dispatch(toggleSidebar())}>
                        About
                    </Button>,
                ]}

                footer={
                    <Tabs defaultActiveKey="1">
                        <TabPane tab="Details" key="1"/>
                        <TabPane tab="Rule" key="2"/>
                    </Tabs>
                }
            >
                <Row>
                    <Statistic title="Status" value="Pending"/>
                    <Statistic
                        title="Price"
                        prefix="$"
                        value={568.08}
                        style={{
                            margin: '0 32px',
                        }}
                    />
                    <Statistic title="Balance" prefix="$" value={3345.08}/>
                </Row>


                
            </PageHeader>



            <Drawer
                title="Basic Drawer"
                placement="right"
                closable={false}
                onClose={onClose}
                visible={state.visible}
                getContainer={false}
                width={'70%'}
                style={{ position: 'absolute' }}
            >
                <p>Some contents...</p>
            </Drawer>
        </>
    )
}

export default Project
